$black: #20252b;
$white: #ffffff;
$golden: #ffd600;

.CvPage {
  background-color: $white;
  color: black;
  position: relative;
  .cv-header {
    background-color: #373d48;
    color: $white;
    padding: 2.3rem 5rem;
    h1 {
      font-weight: bold;
      letter-spacing: 0.1rem;
      font-size: 4.3rem;
      margin: 1rem 0 2rem 0;
    }
    .personal-info {
      display: flex;
      margin-bottom: 0.8rem;
      img {
        width: 2rem;
        filter: invert(100%);
        margin-right: 1rem;
      }
    }
  }
  .cv-body {
    padding: 0 5rem 4rem 5rem;
    h1 {
      border-bottom: 1px solid #373d48;
      padding-bottom: 0.5rem;
      font-size: 2.4rem;
      margin-top: 4rem;
    }
    .cv-experience,
    .cv-education,
    .cv-skills {
      display: grid;
      grid-template-columns: 2fr 2fr 2fr;
      margin-bottom: 2rem;
      .cv-location {
        text-align: right;
      }
      ul {
        margin: 0;
      }
    }
  }
}
.cv-footer {
  display: flex;
  justify-content: space-between;
  margin: 2rem 3rem;
  opacity: 0.7;
  padding-bottom: 1rem;
}
.bold {
  font-weight: bold;
}

.mobile-nav {
  @media (max-width: 40rem) {
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    margin-left: calc(-50vw + 50%);
    background-color: rgba(32, 37, 43, 0.7);
    backdrop-filter: blur(2px);
    justify-content: space-around;
    button {
      background-color: transparent;
      border: none;
    }
  }

  @media (min-width: 120rem) {
    .preview-btn {
      display: none;
    }
    .download-btn {
      position: absolute;
      right: 2rem;
      top: 2rem;
      button {
        border: 1px solid $golden;
        background-color: transparent;
        color: $golden;
        transition: 0.2s all ease-in;
        &:hover {
          background-color: $golden;
          color: $black;
        }
      }
    }
  }
}
